"use client";   // Needed because we use animations + dynamic rendering

import "./globals.css";
import Navbar from "../components/Navbar";
import Footer from "../components/Footer";

export const metadata = {
  title: "Venturemond — Agency",
  description:
    "Full-stack development, UI/UX design and digital strategy for ambitious brands",
};

export default function RootLayout({ children }) {
  return (
    <html lang="en">
      <body className="bg-[#0a0a0b] text-white antialiased">
        {/* Fixed navbar */}
        <Navbar />

        {/* Page content */}
        <main className="min-h-screen">{children}</main>

        {/* Footer */}
        <Footer />
      </body>
    </html>
  );
}
